import React , { Component } from 'react';


class Stript extends Component{
    marginL(){
        return (parseFloat(window.innerWidth/4.5));
    };

    marginT(){
        return (parseFloat(window.innerHeight*0.1));
    };
    render(){
        let left = this.marginL();
        let _width = parseFloat(window.innerWidth);
        let _height = parseFloat(window.innerHeight/1.3);
        return (
            <div className="stript" style={{width:_width,height:_height}}>  
                <div style={{marginLeft:left,position:'absolute',marginTop:"10%"}}>
                      <h style={{fontSize:150,color:'white'}}><strong>SafeGuard</strong></h>
                      <h2 style={{fontSize:50,color:'white',textShadow:"6px 6px 6px black"}}>Secure Your Devices Network</h2>
                </div>
                
            </div>
        );
    };
}


export default Stript;